	// 	}
	// }
	.kt-testimonial-style-card {
		.kt-testimonial-media-inner-wrap {
			margin: 0 0 15px 0;
		}
	}
}
.rtl .kt-testimonial-halign-right .kt-testimonial-meta-wrap {
    flex-direction: row;
}
.rtl .kt-testimonial-meta-wrap .kt-testimonial-meta-name-wrap {
    text-align: right;
}